@layer wcag-ui.core, wcag-ui.foundations, wcag-ui.components;

@layer wcag-ui.core.reset {
  * {
    box-sizing: border-box;
  }

  :where(ul, ol, li, menu, dir) {
    margin: 0;
    padding: 0;
  }

  html {
    font-size: 10px;
  }

  body {
    margin: 0;
  }

  hr {
    border-color: color-mix(in srgb, CanvasText, transparent 80%);
    block-size: 1px;
    inline-size: 80%;
    margin-inline: auto;
    display: block;
  }
}

@layer wcag-ui.core.helpers {
  [sr-only] {
    white-space: nowrap;
    clip: rect(0, 0, 0, 0);
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
}

@layer wcag-ui.foundations.grid-system.variables {
  :root {
    --gs--container--size: 100vw;
    --gs--container--gap: 1.5rem;
    --gs--cols: 12;
    --gs--gap: 1.5rem;
    --gs--v-gap: 1.5rem;
  }
}

@layer wcag-ui.foundations.grid-system.container {
  :where(container, [container]) {
    gap: var(--gs--container--gap);
    inline-size: var(--gs--container--size);
    flex-flow: column;
    block-size: auto;
    margin: 0 auto;
    padding: 0;
    display: flex;

    &:where([fluid]) {
      --gs--container--size: 100%;
    }
  }
}

@layer wcag-ui.foundations.grid-system.container.breakpoints {
  @media screen and (width >= 414px) {
    :root {
      --gs--container--size: 375px;
    }
  }

  @media screen and (width >= 576px) {
    :root {
      --gs--container--size: 540px;
    }
  }

  @media screen and (width >= 768px) {
    :root {
      --gs--container--size: 720px;
    }
  }

  @media screen and (width >= 1024px) {
    :root {
      --gs--container--size: 960px;
    }
  }

  @media screen and (width >= 1366px) {
    :root {
      --gs--container--size: 1200px;
    }
  }

  @media screen and (width >= 1600px) {
    :root {
      --gs--container--size: 1366px;
    }
  }

  @media screen and (width >= 1920px) {
    :root {
      --gs--container--size: 1600px;
    }
  }
}

@layer wcag-ui.foundations.grid-system.row {
  :where(row, [row]) {
    grid-template-columns: repeat(var(--gs--cols), 1fr);
    gap: var(--gs--v-gap) var(--gs--gap);
    grid-template-rows: auto;
    grid-auto-flow: row;
    inline-size: 100%;
    margin: 0;
    display: grid;
  }
}

@layer wcag-ui.foundations.grid-system.column {
  :where(col, [col]) {
    grid-column: auto / span var(--gs--cols);
    max-inline-size: var(--gs--container--size);
    border: 1px solid red;
    flex-flow: column;
    display: flex;
  }
}

